.containerControlOption, .labelControlOption,.buttonControlOption,.textFieldControlOption {
    vertical-align: middle;
    -webkit-transform: perspective(1px) translateZ(0);
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    -webkit-transition-duration: 0.5s;
    transition-duration: 0.5s;
  }
  .containerControlOption:hover, .containerControlOption:focus, .containerControlOption:active,
 .labelControlOption:hover, .labelControlOption:focus, .labelControlOption:active,
  .buttonControlOption:hover, .buttonControlOption:focus, .buttonControlOption:active,
  .textFieldControlOption:hover, .textFieldControlOption:focus, .textFieldControlOption:active {
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
    -webkit-transition-timing-function: cubic-bezier(0.47, 2.02, 0.31, -0.36);
    transition-timing-function: cubic-bezier(0.47, 2.02, 0.31, -0.36);
  }


